import React from 'react'
import CodeExample from '../CodeExample';

const customColorsExamples = `\
$custom-color-names-list: ugly-red ugly-green;
$custom-color-list: #ff0000 #00ff00;
`

const DocsCustomVariables = ({ }) => {
    return (
        <>
            <div className='columns text-small'>

                <div className='column padding-0-5'>

                    <strong>Colors</strong>
                    <table className='small text-left'>

                        <tbody>

                            <tr>
                                <td>$primary</td>
                                <td>$info</td>
                            </tr>
                            <tr>
                                <td>$dark</td>
                                <td>$grey</td>
                            </tr>
                            <tr>
                                <td>$light</td>
                                <td>$danger</td>
                            </tr>
                            <tr>
                                <td>$warning</td>
                                <td>$success</td>
                            </tr>
                            <tr>
                                <td>$white</td>
                                <td>$grey</td>
                            </tr>
                            <tr>
                                <td>$white</td>
                                <td>$grey</td>
                            </tr>

                            <tr>
                                <td>$grey-1</td>
                                <td>$grey-2</td>
                            </tr>
                            <tr>
                                <td>$grey-3</td>
                                <td>$grey-4</td>
                            </tr>

                        </tbody>

                    </table>

                    <div className='padding-top-1 padding-bottom-0-5'>

                        Custom colors can be added like this :
                </div>

                    <CodeExample code={customColorsExamples} language='scss' />

                </div>

                <div className='column padding-0-5'>

                    <strong>Typography</strong>
                    <table className='small text-left'>

                        <tbody>

                            <tr>
                                <td>$font-size</td>
                                <td>font size on mobile in px</td>
                            </tr>

                            <tr>
                                <td>$font-size-desktop</td>
                                <td>font size on desktop in px</td>
                            </tr>
                            <tr>
                                <td>$line-height</td>
                                <td>line height on mobile (in px)</td>
                            </tr>
                            <tr>
                                <td>$line-height-desktop</td>
                                <td>line height on desktop (in px)</td>
                            </tr>
                            <tr>
                                <td>$size-ratio</td>
                                <td>bigger the ratio is, bigger the difference is between h1,h2,h3...</td>
                            </tr>
                            <tr>
                                <td>$measure</td>
                                <td>Width in rem of a measure block</td>
                            </tr>
                            <tr>
                                <td>$container-max-width</td>
                                <td>Container width in rem</td>
                            </tr>
                        </tbody>

                    </table>
                </div>
            </div>
            <div className='text-small text-center margin-top-1'>
                More informations here :
                {' '}
                <a target='_blank' rel="noopener noreferrer"
                    href='https://github.com/anthonyjeamme/kitokocss/blob/master/src/base/initial-variables.scss'>https://github.com/anthonyjeamme/kitokocss/blob/master/src/base/initial-variables.scss</a>
            </div>

        </>
    )
}
export default DocsCustomVariables