import React from 'react'
import CodeExample from '../CodeExample';

const DocsCustomIntroduction = ({ }) => {

    const customCodeExample = `\
// Customize
$primary: #112233;
$font-size: 16;

// Then import kitoko
@include 'kitoko';
`

    return (
        <div className='text-small margin-bottom-1'>

            <div className='margin-bottom-0-5'>

                To custom Kitoko for your needs, you have to create a scss/sass file and set variables before including the kitoko lib
            </div>

            <CodeExample code={customCodeExample} language='scss' />
        </div>
    )
}
export default DocsCustomIntroduction